import { onAction } from 'mobx-state-tree'
import PhotoGalleryStore from './PhotoGalleryStore'
// import defaultData from '../data/default-data.json'
import photoFilesListData from '../data/photo-list.json'
import { loadFirebaseData, saveFirebaseData } from '../services/db-functions'

const store = PhotoGalleryStore.create({
  isReady: false,
  galleryRowList: [
    {
      id: 'initial',
      elementList: [
        {
          type: 'text',
          content: '# loading ...',
        },
      ],
      numPhotos: 1,
      layout: 'FULL_WIDTH',
      height: 20,
    },
  ],

  photoList: {
    files: photoFilesListData,
    basePath: 'photos',
  },
})

const actionsToSave = [
  'exitEditMode',
  'moveDown',
  'moveUp',
  'remove',
  'addRowAfter',
  'addRowBefore',
]

onAction(store, ({ name }) => {
  if (actionsToSave.includes(name)) {
    console.log('salva accin', store.output)
    saveFirebaseData(store.output)
  }
})

loadFirebaseData().then(({galleryRowList}) => {
  store.initData(galleryRowList)
})

window.store = store
export default store
