import React from 'react'
import { observer } from 'mobx-react-lite'
import FullScreenImage from './components/full-screen-image/FullScreenImage'
import FullScreenNavigation from './components/full-screen-navigation/FullScreenNavigation'
import FullScreenClose from "./components/full-screen-close/FullScreenClose";
import FullScreenBg from "./components/full-screen-bg/FullScreenBg";
import './full-screen-viewer.css'

function FullScreenViewer({ store }) {
  if (!store.isFullScreen) return false

  return (
    <div className="full-screen-viewer">
      <FullScreenImage image={store.fullScreen.currentImage} />
      <FullScreenNavigation
        next={store.fullScreen.nextImage}
        prev={store.fullScreen.prevImage}
      />
      <FullScreenClose close={store.fullScreen.exitFullScreen} />
      <FullScreenBg close={store.fullScreen.exitFullScreen} />
    </div>
  )
}

export default observer(FullScreenViewer)
