import React from 'react'
import { observer } from 'mobx-react-lite'
import useMst from '../../hooks/useMst'
import RowLayout from '../../components/row-layout/RowLayout'
import NavigationButton from '../../components/navigation-button/NavigationButton'
import FullScreenViewer from '../../components/full-screen-viewer/FullScreenViewer'
import './gallery-view.css'

function GalleryView() {
  const store = useMst()
  const firstRow = store?.galleryRowList?.[0]
  if (!firstRow) return null
  
  return (
    <div className="gallery-view">
      {store.galleryRowList.map((row) => (
        <RowLayout key={row.key} row={row} editMode={false} />
      ))}
      <FullScreenViewer store={store} />
      <NavigationButton link="/">Presentacin</NavigationButton>
    </div>
  )
}

export default observer(GalleryView)
