import React from 'react'
import { observer } from 'mobx-react-lite'
import { getBackgroundPosition } from './util/helpers'
import './photo.css'

function Photo({ src, position, fitMode, openFullScreen }) {
  return (
    <div
      className="photo"
      onClick={openFullScreen}
      style={{
        backgroundPosition: getBackgroundPosition({ position, fitMode }),
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url("./${src}")`,
      }}
    />
  )
}

export default observer(Photo)
