import React from 'react'
import { observer } from 'mobx-react-lite'
import Range from '../range/Range'
import Dropdown from '../dropdown/Dropdown'
import FitModes from '../../constants/fitModes'
import './photo-config.css'
import { getRoot } from 'mobx-state-tree'

const fitModeList = Object.keys(FitModes)

function PhotoConfig({ photo }) {
  const {
    position,
    setPosition,
    photoName,
    hasPhoto,
    unselect,
    fitMode,
    setFitMode,
    useText,
  } = photo

  function openPhotoSelector() {
    const photoListInstance = getRoot(photo).photoList
    photoListInstance.show()
  }

  function handlePosition(e) {
    setPosition(parseInt(e.target.value))
  }

  function exit() {
    const photoListInstance = getRoot(photo).photoList
    unselect()
    photoListInstance.hide()
    // exitEditMode()
  }

  return (
    <div className="photo-config">
      <div className="left-controls">
        <label className="form-label">
          <span className="label-text">layout</span>
          <Dropdown
            value={fitMode}
            onChange={setFitMode}
            itemList={fitModeList}
          />
        </label>
        <label className="form-label">
          <span className="label-text">Position</span>
          <Range
            type="range"
            min={0}
            max={100}
            value={position}
            setValue={handlePosition}
          />
        </label>
        <label className="form-label image-selector">
          <button onClick={openPhotoSelector}>
            {hasPhoto ? `change photo: ${photoName}` : 'add photo'}
          </button>
        </label>
      </div>
      <div className="right-controls">
        <button className="form-button" onClick={useText}>
          use text
        </button>
        <button className="form-button" onClick={exit}>
          exit edit photo
        </button>
      </div>
    </div>
  )
}

export default observer(PhotoConfig)
