import React from 'react'
import ReactMarkdown from 'react-markdown'
import {observer} from 'mobx-react-lite'
import TextEditor from '../text-editor/TextEditor'
import './text-container.css'

function TextContainer({textContent, editMode}) {
  const {content, isSelected, select} = textContent

  function handleEdit() {
    if (isSelected || !editMode) return false
    select()
  }

  return (
    <div className={`text-container${isSelected ? ' selected' : ''}`} onClick={handleEdit}>
      <div className="text-container__wrapper">
        {isSelected ? (
          <TextEditor textContent={textContent}/>
        ) : (
          <ReactMarkdown>{content}</ReactMarkdown>
        )}
      </div>
    </div>
  )
}

export default observer(TextContainer)