import React, {useEffect} from 'react'
import {observer} from 'mobx-react-lite'
import {useNavigate} from 'react-router-dom'
import useMst from '../../hooks/useMst'
import RowConfig from '../../components/row-config/RowConfig'
import RowLayout from '../../components/row-layout/RowLayout'
import FilesManager from "../../components/files-manager/FilesManager";

function RowEdit() {
  const navigate = useNavigate()
  const store = useMst()
  const row = store?.selectedRow

  useEffect(() => {
    if(!row) navigate('../gallery-edit')
    return () => {
      store?.selectedRow?.exitEditMode()
    }
  }, [])

  return !row ? null : (
    <div className="row-edit">
      <div className="edit-controls">
        <RowConfig row={row}/>
      </div>
      <div className="row-view">
        <RowLayout row={row} editMode/>
      </div>
      <FilesManager />
    </div>
  )
}

export default observer(RowEdit)
