import when from '@aqrojo/when'
import layoutTypes from '../../../constants/layoutTypes'

export default function getDefaultLayout(photosLength) {
  const eq = (n) => n === photosLength
  return when()
    .case(eq(1), () => layoutTypes.FULL_WIDTH)
    .case(eq(2), () => layoutTypes.C50_C50)
    .case(eq(3), () => layoutTypes.C33_C33_C33)
    .case(eq(4), () => layoutTypes.c25r2_c25_c50)
    .resolve(() => layoutTypes.FULL_WIDTH)
}
