import { initializeApp } from 'firebase/app'
import {
  getFirestore,
  setDoc,
  doc,
  getDoc,
  collection,
} from 'firebase/firestore'

initializeApp({
  apiKey: 'AIzaSyCTys6F_u6JerNBEBb3w-aVCDP-ri8gVx4',
  authDomain: 'margui-photo-gallery.firebaseapp.com',
  projectId: 'margui-photo-gallery',
  storageBucket: 'margui-photo-gallery.appspot.com',
  messagingSenderId: '185423798603',
  appId: '1:185423798603:web:eb6544e2d709fb820a48ce',
})

const db = getFirestore()

const mainDocument = doc(db, 'gallery/photoGallery')

export const saveFirebaseData = (json) => {
  setDoc(mainDocument, json)
}

export const loadFirebaseData = async () => {
  const result = await getDoc(mainDocument)
  return result.data()
}
