import fitModes from '../../../constants/fitModes'

export function getFitClass(fitMode) {
  return `photo__${fitMode.toLowerCase()}`
}

export function getBackgroundSize({ size, fitMode, isFullWidth }) {
  if(isFullWidth) return 'cover'
  return fitMode === fitModes.HORIZONTAL ? `${size}% auto` : `auto ${size}%`
}

export function getBackgroundPosition({ position, fitMode }) {
  return fitMode === fitModes.HORIZONTAL
    ? `center ${position}%`
    : `${position}% center`
}
