import React from 'react'
import {Observer} from 'mobx-react-lite'

export default function Dropdown({onChange, value, itemList, itemPattern}) {
  return (
    <Observer>
      {() => (
        <select onChange={({target}) => onChange(target.value)} value={value}>
          {itemList.map((item, idx) => (
            <option key={`${item}_${idx}`} value={item}>
              {itemPattern ? `${itemPattern} ${idx + 1}` : item}
            </option>
          ))}
        </select>
      )}
    </Observer>
  )
}

