import { destroy, getSnapshot, types } from 'mobx-state-tree'
import FullScreenModel from './FullScreenModel'
import GalleryRowModel from './GalleryRowModel'
import layoutTypes from '../constants/layoutTypes'
import PhotoListModel from './PhotoListModel'
import PresentationModel from './PresentationModel'

const initialFullScreen = { selectedImage: -1 }

const photoElement = { type: 'photo' }

const defaultRowData = {
  elementList: [photoElement, photoElement, photoElement],
  layout: layoutTypes.C33_C33_C33,
}

const PhotoGalleryModel = types
  .model('PhotoGalleryModel', {
    isReady: false,
    galleryRowList: types.array(GalleryRowModel),
    fullScreen: types.optional(FullScreenModel, initialFullScreen),
    presentation: types.optional(PresentationModel, {}),
    targetRow: types.optional(types.string, ''),
    photoList: PhotoListModel,
    notificationCounter: types.optional(types.number, 0),
  })
  .actions((state) => ({
    addGalleryRow() {},
    initData(data) {
      state.galleryRowList = data
      state.isReady = true
    },
    removeGalleryRow(row) {
      state.setTargetRow()
      destroy(row)
    },
    setTargetRow(rowId = '') {
      state.targetRow = rowId
    },
    addNewRowAt(index, data = defaultRowData) {
      state.galleryRowList.splice(index, 0, data)
      const newRow = state.galleryRowList[index]
      newRow.enterEditMode()
      return newRow
    },
    updateRowPosition(currentIndex, targetIndex) {
      const row = state.galleryRowList[currentIndex]
      const rowData = getSnapshot(row)
      state.removeGalleryRow(row)
      state.addNewRowAt(targetIndex, rowData)
      state.setTargetRow(rowData.id)
      state.updateNotificationCounter()
    },
    updateNotificationCounter() {
      state.notificationCounter += 1
    },
  }))
  .views((state) => ({
    get selectedRow() {
      return state.galleryRowList.find((row) => row.isInEditMode)
    },

    get imageList() {
      return state.galleryRowList.reduce((acc, next) => {
        const photos = next.elementList.filter(
          (item) =>
            isPhoto(item) && {
              file: item.fileName,
              category: item.category,
            }
        )

        return acc.concat(photos)
      }, [])
    },

    get isFullScreen() {
      return state.fullScreen.selectedImage > -1
    },

    get output() {
      return {
        galleryRowList: state.galleryRowList.map((row) => ({
          id: row.id,
          elementList: row.output,
          numPhotos: row.numPhotos,
          layout: row.layout,
          height: row.height,
        })),
      }
    },
  }))

function isPhoto(item) {
  return item.type === 'photo' && item.hasPhoto
}

export default PhotoGalleryModel
