import { getParent, getRoot, types } from 'mobx-state-tree'
import fitModes from '../constants/fitModes'
import createPhotoId from '../util/createPhotoId'

const PhotoModel = types
  .model('PhotoModel', {
    type: types.literal('photo'),
    category: types.maybe(types.string),
    src: types.optional(types.string, ''),
    position: types.optional(types.number, 50),
    isSelected: types.optional(types.boolean, false),
    fitMode: types.optional(
      types.enumeration(Object.keys(fitModes)),
      fitModes.HORIZONTAL
    ),
  })
  .actions((state) => {
    const filesManager = getRoot(state).photoList
    return {
      addPhoto({ src, category = '' }) {
        state.src = src
        state.category = category
        state.select()
      },
      setFitMode(fitMode) {
        state.fitMode = fitMode
      },
      setPosition(position) {
        state.position = position
      },
      select() {
        getParent(state, 2).clearSelection(state)
        state.isSelected = true
        filesManager.selectCategory(state.category)
        filesManager.selectPhoto(state.id)
      },
      unselect() {
        state.isSelected = false
      },
      handleSelection() {
        // if(filesManager.opened) return false
        state.isSelected ? state.unselect() : state.select()
        filesManager.show()
      },
      useText() {
        getParent(state, 2).replaceByText(state)
      },
      clear() {
        state.src = ''
        state.category = ''
        state.unselect()
      },
    }
  })
  .views((state) => ({
    get hasPhoto() {
      return state.src.length > 0
    },

    get fileName() {
      if (!state.hasPhoto) return ''
      const textParts = state.src.split('/')
      return textParts[textParts.length - 1]
    },

    get photoName() {
      if (!state.hasPhoto) return ''
      return state.fileName.length > 12
        ? state.fileName.substring(0, 10) + '...'
        : state.fileName
    },

    get id() {
      if (!state.hasPhoto) return ''
      return createPhotoId({
        category: state.category,
        src: state.fileName,
      })
    },

    get index() {
      if (!state.hasPhoto) return ''
      return getRoot(state).imageList.findIndex((item) => item.id === state.id)
    },

    get output() {
      return {
        type: state.type,
        category: state.category,
        src: state.src,
        position: state.position,
        fitMode: state.fitMode,
      }
    },
  }))

export default PhotoModel
