import {destroy, getParent, getRoot, types} from 'mobx-state-tree'
import { v4 as uuid } from '@lukeed/uuid'
import PhotoModel from './PhotoModel'
import layoutTypes from '../constants/layoutTypes'
import layoutCategories from '../constants/layoutCategories'
import when from '@aqrojo/when'
import TextModel from './TextModel'

const content = types.union(PhotoModel, TextModel)

const GalleryRowModel = types
  .model('GalleryRowModel', {
    id: types.optional(types.string, () => uuid()),
    elementList: types.array(content),
    numPhotos: types.number,
    layout: types.enumeration(Object.keys(layoutTypes)),
    height: 40,
    isInEditMode: false,
  })
  .actions((state) => ({
    setLayout(layout) {
      state.layout = layout
    },
    setNumPhotos(numPhotos) {
      state.layout = layoutCategories[numPhotos - 1][0]
      state.numPhotos = numPhotos
      state.adjustArrayLength(numPhotos)
    },
    adjustArrayLength(numPhotos) {
      const diff = state.elementList.length - numPhotos
      when()
        .case(diff > 0, () => state.decreaseByNumPhotos(Math.abs(diff)))
        .case(diff < 0, () => state.increaseByNumPhotos(Math.abs(diff)))
        .resolve()
    },
    increaseByNumPhotos(diff) {
      const newSize = state.elementList.length + diff
      while (state.elementList.length < newSize) {
        state.elementList.push({ type: 'photo' })
      }
    },
    decreaseByNumPhotos(diff) {
      state.elementList.splice(state.elementList.length - diff)
    },
    setHeight(height) {
      state.height = height
    },
    enterEditMode() {
      state.isInEditMode = true
      getParent(state, 2).setTargetRow(state.id)
    },
    exitEditMode() {
      state.isInEditMode = false
      state.clearSelection()
      getRoot(state).photoList.hide()
    },
    remove() {
      state.exitEditMode()
      getParent(state, 2).removeGalleryRow(state)
    },
    clearSelection(ignore) {
      state.elementList.forEach((photo) => photo !== ignore && photo.unselect())
    },
    addRowBefore() {
      getParent(state, 2).addNewRowAt(Math.max(state.index - 1, 0))
    },
    addRowAfter() {
      getParent(state, 2).addNewRowAt(state.index + 1)
    },
    moveUp() {
      if (state.index === 0) return
      getParent(state, 2).updateRowPosition(state.index, state.index - 1)
    },
    moveDown() {
      if (state.index + 1 === getParent(state).length) return
      getParent(state, 2).updateRowPosition(state.index, state.index + 1)
    },
    replaceByText(element) {
      state.replaceByItem({ itemToReplace: element, newItem: { type: 'text' } })
    },
    replaceByPhoto(element) {
      state.replaceByItem({
        itemToReplace: element,
        newItem: { type: 'photo' },
      })
    },
    replaceByItem({ itemToReplace, newItem }) {
      state.elementList.forEach((item, idx) => {
        if (item === itemToReplace) {
          destroy(itemToReplace)
          state.elementList.splice(idx, 0, newItem)
        }
      })
    },
  }))
  .views((state) => ({
    get index() {
      return getParent(state).findIndex((item) => item.id === state.id)
    },
    get selectedElement() {
      return (
        state.elementList.find((element) => element.isSelected) || undefined
      )
    },
    get imageSelected() {
      const { selectedElement } = state
      return selectedElement?.type === 'photo' ? selectedElement : undefined
    },
    get textSelected() {
      const { selectedElement } = state
      return selectedElement?.type === 'text' ? selectedElement : undefined
    },
    get layoutList() {
      return layoutCategories[state.numPhotos - 1]
    },
    get output() {
      return state.elementList.map((element) => element.output)
    },
  }))

export default types.snapshotProcessor(GalleryRowModel, {
  preProcessor(snapshot) {
    return {
      ...snapshot,
      numPhotos: snapshot.elementList.length,
    }
  },
})
