import React, { useCallback } from 'react'
import { useNavigate } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import './edit-row-controls.css'

function EditRowControls({ row }) {
  const navigate = useNavigate()

  const goToEdit = useCallback(() => {
    navigate('../row-edit')
  }, [navigate])

  const edit = useCallback(() => {
    row.enterEditMode()
    goToEdit()
  }, [row, goToEdit])

  const moveUp = useCallback(() => {
    row.moveUp()
  }, [row])

  const moveDown = useCallback(() => {
    row.moveDown()
  }, [row])

  const addRowAfter = useCallback(() => {
    row.addRowAfter()
    goToEdit()
  }, [row, goToEdit])

  const addRowBefore = useCallback(() => {
    row.addRowBefore()
    goToEdit()
  }, [row, goToEdit])

  const removeRow = useCallback(() => {
    row.remove()
    // mover el scroll hacia arriba
    goToEdit()
  }, [row, goToEdit])

  return (
    <div className="edit-row-controls">
      <div className="center-controls">
        <button
          className="basic-button add-row-before-button"
          onClick={addRowBefore}
        >
          +
        </button>
        <button className="basic-button edit-row-button" onClick={edit}>
          edit
        </button>

        <button
          className="basic-button add-row-after-button"
          onClick={addRowAfter}
        >
          +
        </button>
      </div>
      <div className="floating-right-controls">
        <button className="basic-button remove-button" onClick={removeRow}>
          remove
        </button>
      </div>
      <div className="floating-left-controls">
        <button className="basic-button move-up-button" onClick={moveUp}>
          up
        </button>
        <button className="basic-button move-dow-button" onClick={moveDown}>
          down
        </button>
      </div>
    </div>
  )
}

export default observer(EditRowControls)
