import { HashRouter, Route, Routes } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import Home from './pages/home/Home'
import GalleryEdit from './pages/gallery-edit/GalleryEdit'
import GalleryView from './pages/gallery-view/GalleryView'
import RowEdit from './pages/row-edit/RowEdit'
import FilesManager from './components/files-manager/FilesManager'
import useMst from './hooks/useMst'
import Loading from "./components/loading/Loading";
import './App.css'

function App() {
  const { isReady } = useMst()
  if (!isReady) return <Loading />
  return (
    <HashRouter>
      <Routes>
        <Route path="gallery-view" element={<GalleryView />} />
        <Route path="gallery-edit" element={<GalleryEdit />} />
        <Route path="row-edit" element={<RowEdit />} />
        <Route path="files-manager" element={<FilesManager />} />
        <Route path="/" element={<Home />} />
      </Routes>
    </HashRouter>
  )
}

export default observer(App)
