import React from 'react'
import {observer} from 'mobx-react-lite'
import {ReactComponent as NavigationIcon} from '../../../../assets/icon/navigation-icon.svg'
import './full-screen-navigation.css'

function FullScreenNavigation({next, prev}) {
  return (
    <div className="full-screen-navigation">
      <button onClick={prev} role="button">
        <NavigationIcon className="navigation-icon-prev"/>
      </button>
      <button onClick={next} role="button">
        <NavigationIcon className="navigation-icon-next"/>
      </button>
    </div>
  )
}

export default observer(FullScreenNavigation)