import { getParent, getRoot, types } from 'mobx-state-tree'
import createPhotoId from '../util/createPhotoId'

const FileModel = types
  .model('FileModel', {
    src: types.string,
    category: types.string,
    isSelected: types.optional(types.boolean, false),
  })
  .actions((state) => ({
    handleSelection() {
      state.isSelected ? state.unselect() : state.select()
    },
    unselect() {
      state.isSelected = false
    },
    select() {
      getParent(state).forEach((file) => file.unselect())
      state.isSelected = true
    },
  }))
  .views((state) => ({
    get id() {
      return createPhotoId(state)
    },
    getInUse() {
      const { imageList } = getRoot(state)
      console.log('current', state.id)
      return imageList.find((img) => {
        console.log(img.id)
        return img.id === state.id
      })
    },

    get isInUse() {
      const { imageList } = getRoot(state)
      return imageList.find((img) => img.id === state.id)
    },
    get export() {
      const { basePath } = getParent(state, 2)
      return {
        src: `${basePath}/${state.category}/${state.src}`,
        category: state.category,
      }
    },
  }))

export default FileModel
