import React from "react";
import {observer} from "mobx-react-lite";
import './text-editor.css'

function TextEditor({ textContent }) {
  const { content, setContent } = textContent

  function handleOnChange(e) {
    setContent(e.target.value)
  }

  return (
    <div className="text-editor" data-replicated-value={content}>
      <textarea className="text-editor__content" onChange={handleOnChange} defaultValue={content} />
    </div>
  )
}

export default observer(TextEditor)