import React from 'react'
import { observer } from 'mobx-react-lite'
import cn from '@aqrojo/class-names'
import SelectImageButton from '../../../select-image-button/SelectImageButton'
import { ReactComponent as CheckIcon } from '../../../../assets/icon/check.svg'
import './file-thumb.css'

function FileThumb({ file, onSelect }) {
  const url = file.export.src

  function handleSelection() {
    file.handleSelection()
    onSelect(file)
  }

  return (
    <div className={cn('file-thumb', { selected: file.isSelected })}>
      <SelectImageButton
        onClick={handleSelection}
        isSelected={file.isSelected}
      />
      <CheckIcon className={cn('file-thumb__icon', { used: file.isInUse })} />
      <img className="file-thumb__image" src={url} alt="" />
    </div>
  )
}

export default observer(FileThumb)
