import React, { useEffect, useRef, useState } from 'react'
import {observer} from "mobx-react-lite";
import './presentation.css';

const timeOut = 3500
const exitTimeOut = 300
const animationStates = ['in', 'out']

function Presentation({ image, next, startPresentation }) {
  const [animation, setAnimation] = useState(0)

  const interval = useRef(0)

  function fadeIn() {
    setAnimation(0)
  }

  function fadeOut() {
    setAnimation(1)
    interval.current = setTimeout(next, exitTimeOut)
  }

  function exit () {
    clearTimeout(interval.current)
  }

  useEffect(() => {
    startPresentation()
    return () => exit()
  }, [])

  useEffect(() => {
    fadeIn()
    interval.current = setTimeout(fadeOut, timeOut)
  }, [image.src])


  return image?.src ? (
    <div
      className={`full-screen-image presentation-image presentation-image--fade-${animationStates[animation]}`}
      style={{ backgroundImage: `url("./${image.src}")` }}
    />
  ): <p>no image ...</p>
}

export default observer(Presentation)
