import React from 'react'
import when from '@aqrojo/when'
import { useNavigate } from 'react-router-dom'
import LayoutConfig from '../layout-config/LayoutConfig'
import PhotoConfig from '../photo-config/PhotoConfig'
import { observer } from 'mobx-react-lite'
import TextConfig from '../text-config/TextConfig'

function RowConfig({ row }) {
  const navigate = useNavigate()

  function exitEditMode() {
    row.exitEditMode()
    navigate('../gallery-edit')
  }

  return (
    <div>
      {when()
        .case(row.imageSelected, () => (
          <PhotoConfig
            photo={row.imageSelected}
            exitEditMode={exitEditMode}
          />
        ))
        .case(row.textSelected, () => <TextConfig text={row.textSelected} />)
        .resolve(() => (
          <LayoutConfig row={row} exitEditMode={exitEditMode} />
        ))}
    </div>
  )
}

export default observer(RowConfig)
