import React from 'react'
import { observer } from 'mobx-react-lite'
import './select-image-button.css'
import cn from '@aqrojo/class-names'

function SelectImageButton({ onClick, isSelected }) {
  // const actionText = (isSelected ? 'unselect' : 'select') + ' image'
  return (
    <button className="select-image-button" onClick={onClick}>
      <div
        className={cn('select-image-button__icon', { selected: isSelected })}
      />
    </button>
  )
}

export default observer(SelectImageButton)
