import React from 'react'
import './file-categories.css'
import {observer} from "mobx-react-lite";

function FileCategories({ categories, selectCategory }) {
  function handleCategory(e) {
    selectCategory(e.target.textContent)
  }

  return (
    <ul className="files-categories">
      {categories.map((category, idx) => (
        <li className="files-categories__item" key={category}>
          <a className="files-categories__link" onClick={handleCategory}>
            {category}
          </a>
        </li>
      ))}
    </ul>
  )
}

export default observer(FileCategories)
