import React from 'react'
import { observer } from 'mobx-react-lite'
import PhotoContainer from '../photo-container/PhotoContainer'
import getDefaultLayout from './util/getDefaultLayout'
import Photo from '../photo/Photo'
import TextContainer from '../text-container/TextContainer'
import getInnerWidth from '../../util/getInnerWidth'
import { getRoot } from 'mobx-state-tree'

import './row-layout.css'

export function RowLayout({ row, editMode }) {
  const { id, elementList, layout, isInEditMode, height } = row
  const len = elementList.length
  const layoutDesign = layout || getDefaultLayout(len)
  const rowHeight = (height * getInnerWidth()) / 100

  function openFullScreen(idx) {
    if (editMode) return false
    getRoot(row).fullScreen.enterFullScreen(idx)
  }

  return (
    <div
      id={id}
      className={`row-layout ${layoutDesign}`}
      // style={{ minHeight: `${height}vw`}}
      style={{ minHeight: rowHeight, maxWidth: getInnerWidth() }}
    >
      {elementList.map((item, idx) =>
        item.type === 'text' ? (
          <TextContainer
            key={`text_content_${idx}`}
            textContent={item}
            editMode={editMode !== false}
          />
        ) : (
          <PhotoContainer
            key={`${item.src || 'empty'}_${idx}`}
            elementId={`element_${idx}`}
            isInEditMode={isInEditMode && editMode}
            photo={item}
          >
            {item.src && (
              <Photo
                src={item.src}
                fitMode={item.fitMode}
                position={item.position}
                openFullScreen={() => openFullScreen(item.index)}
              />
            )}
          </PhotoContainer>
        )
      )}
    </div>
  )
}

export default observer(RowLayout)
