import React, { useCallback, useState } from 'react'
import { observer } from 'mobx-react-lite'
import EditRowControls from '../edit-row-controls/EditRowControls'
import RowLayout from '../row-layout/RowLayout'
import './gallery-edit-row.css'

function GalleryEditRow({ row, scrollToRow }) {
  const [isHover, setIsHover] = useState(false)
  const handleHover = useCallback(
    (e) => setIsHover(e.type === 'mouseover'),
    [setIsHover]
  )

  return (
    <div
      className="gallery-edit-row"
      onMouseOver={handleHover}
      onMouseLeave={handleHover}
    >
      {isHover && <EditRowControls row={row} />}
      <RowLayout row={row} />
    </div>
  )
}

export default observer(GalleryEditRow)