import React from 'react'
import cn from '@aqrojo/class-names'
import {observer} from 'mobx-react-lite'
import FileControls from './components/file-controls/FileControls'
import FileCategories from './components/file-categories/FileCategories'
import FileList from './components/file-list/FileList'
import useMst from '../../hooks/useMst'
import './files-manager.css'

function FilesManager() {
  const {photoList} = useMst()

  return (
    <div className={cn('files-manager', {show: photoList.opened})}>
      <FileControls handleSave={photoList.hide}/>
      <FileCategories
        categories={photoList.categoryList}
        selectCategory={photoList.selectCategory}
      />
      <FileList
        files={photoList.filesCategory}
        onSelect={photoList.changePhoto}
      />
    </div>
  )
}

export default observer(FilesManager)
