import React, {useCallback, useEffect} from 'react'
import { observer } from 'mobx-react-lite'
import useMst from '../../hooks/useMst'
import GalleryEditRow from '../../components/gallery-edit-row/GalleryEditRow'

function GalleryEdit() {
  const store = useMst()


  const scrollToRow = useCallback((elementId) => {
    const element = document.getElementById(elementId)
    element?.scrollIntoView({ behavior: 'smooth' })
  }, [])

  useEffect(() => {
    store.targetRow && scrollToRow(store.targetRow)
  }, [store.targetRow, store.notificationCounter])

  return (
    <div className="gallery-edit">
      {store.galleryRowList.map((row) => (
        <GalleryEditRow row={row} key={row.id} />
      ))}
    </div>
  )
}

export default observer(GalleryEdit)
