import {getParent, types} from 'mobx-state-tree'
import FileModel from './FileModel'

const PhotoListModel = types
  .model('PhotoListModel', {
    files: types.optional(types.array(FileModel), []),
    basePath: types.optional(types.string, ''),
    selectedCategory: types.optional(types.string, ''),
    opened: types.optional(types.boolean, false),
  })
  .actions((state) => ({
    afterCreate() {
      if (state.selectedCategory.length === 0)
        state.selectCategory(state.categoryList[0])
    },
    selectCategory(categoryName) {
      if (!state.categoryList.includes(categoryName)) return false
      state.selectedCategory = categoryName
    },
    selectPhoto(photoId) {
      const targetFile = state.files.find((file) => file.id === photoId)
      targetFile ? targetFile.select() : state?.fileSelected?.unselect()
    },
    show() {
      state.opened = true
    },
    hide() {
      state.opened = false
      state?.fileSelected?.unselect()
    },
    changePhoto(file) {
      const currentImage = getParent(state)?.selectedRow.imageSelected
      if (!currentImage) return false
      currentImage.addPhoto(file.export)
    },
  }))
  .views((state) => ({
    get categoryList() {
      return state.files.reduce((acc, next) => {
        const {category} = next
        return acc.includes(category) ? acc : [...acc, category]
      }, [])
    },
    get filesCategory() {
      const {selectedCategory, files} = state
      return files.filter((image) => image.category === selectedCategory)
    },
    get fileSelected() {
      return state.files.find((file) => file.isSelected)
    },
  }))

export default PhotoListModel
