import React from 'react'
import { Observer } from 'mobx-react-lite'
import NumericInput from '../numeric-input/NumericInput'
import Dropdown from '../dropdown/Dropdown'
import Range from '../range/Range'
import './layout-config.css'

function LayoutConfig({
  numPhotos,
  setNumPhotos,
  height,
  setHeight,
  layoutList,
  layout,
  setLayout,
  exitEditMode,
}) {
  function handleHeight(e) {
    setHeight(parseInt(e.target.value))
  }

  return (
    <div className="layout-config">
      <div className="left-controls">
        <label className="form-label">
          <span className="label-text">NumPhotos</span>
          <NumericInput
            min={1}
            max={4}
            value={numPhotos}
            onChange={setNumPhotos}
          />
        </label>
        <label className="form-label">
          <span className="label-text">layout</span>
          <Dropdown
            value={layout}
            onChange={setLayout}
            itemList={layoutList}
            itemPattern="layout"
          />
        </label>
        <label className="form-label">
          <span className="label-text">RowHeight</span>
          <Range
            type="range"
            min={20}
            max={100}
            value={height}
            setValue={handleHeight}
          />
        </label>
      </div>
      <div className="right-controls">
        <button className="form-button" onClick={exitEditMode}>
          save row
        </button>
      </div>
    </div>
  )
}

function WithStore({ row, exitEditMode }) {
  return (
    <Observer>
      {() => (
        <LayoutConfig
          numPhotos={row.numPhotos}
          setNumPhotos={row.setNumPhotos}
          height={row.height}
          setHeight={row.setHeight}
          layoutList={row.layoutList}
          layout={row.layout}
          setLayout={row.setLayout}
          exitEditMode={exitEditMode}
        />
      )}
    </Observer>
  )
}

export default WithStore
