import React from 'react'
import { observer } from 'mobx-react-lite'
import NavigationButton from '../../components/navigation-button/NavigationButton'
import Presentation from '../../components/presentation/Presentation'
import useMst from '../../hooks/useMst'
import SoundManager from "../../components/sound-manager/SoundManager";
import './home.css'

const songs = ["./songs/song-01.mp3", "./songs/song-02.mp3", "./songs/song-03.mp3", "./songs/song-04.mp3"]

function Home() {
  const { presentation } = useMst()

  return (
    <div className="home-page">
      <SoundManager songs={songs}/>
      <Presentation
        image={presentation.currentImage}
        next={presentation.nextImage}
        startPresentation={presentation.startPresentation}
      />
      <NavigationButton link="/gallery-view">Galera</NavigationButton>
    </div>
  )
}

export default observer(Home)
