import React from 'react'
import { observer } from 'mobx-react-lite'

function Range({ value, setValue, min = 0, max = 100 }) {
  return (
    <input
      className="input-range"
      type="range"
      min={min}
      max={max}
      value={value}
      onChange={setValue}
    />
  )
}

export default observer(Range)