import React from 'react'
import { observer } from 'mobx-react-lite'
import AddContentButton from '../add-content-button/AddContentButton'
import SelectImageButton from '../select-image-button/SelectImageButton'
import './photo-container.css'
import cn from '@aqrojo/class-names'

function PhotoContainer({ children, isInEditMode, elementId, photo }) {
  function addContent() {
    photo.handleSelection()
  }

  const showAddItem = isInEditMode && !children && !photo.isSelected
  const showSelectImage = isInEditMode && children
  return (
    <div
      className={cn('photo-container', elementId, {
        selected: photo.isSelected,
      })}
    >
      {children}
      {showAddItem && <AddContentButton onClick={addContent} />}
      {showSelectImage && (
        <SelectImageButton
          onClick={photo.handleSelection}
          isSelected={photo.isSelected}
        />
      )}
    </div>
  )
}

export default observer(PhotoContainer)
