import React from 'react'
import {observer} from 'mobx-react-lite'
import './text-config.css'

function TextConfig({text}) {
  const {unselect, usePhoto} = text

  return (
    <div className="text-config">
      <button className="form-button" onClick={usePhoto}>
        use photo
      </button>
      <button className="form-button" onClick={unselect}>
        save text
      </button>
    </div>
  )
}

export default observer(TextConfig)
